/* LeftBar.css */

.left-bar {
    width: 15%;
    height: 100%;
    background-color: #333;
    color: white;
    padding: 20px;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  li {
    margin-bottom: 10px;
    cursor: pointer;
  }
  
  li:hover {
    color: #4caf50;
  }