/* App.css */

.App {
  height: 100vh;
  display: flex;
}

.App-header {
  flex: 1; /* Растягиваем header на всю доступную ширину */
}

.app-container {
  display: flex;
  height: 100%;
}

.main-content {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}



.image-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px; /* Пример отступа для контента справа */
}

.image-container img {
  width: 100%;
  max-width: 100%;
  height: auto;
  border: 2px solid blue; /* Добавьте границу вокруг изображения */
  box-sizing: border-box; /* Учтите границу в общем размере элемента */
}

.custom-link {
  text-decoration: underline; /* Подчеркивание текста ссылки */
  color: #0000ff; /* Замените #ff0000 цветом, который вы хотите использовать */
}